export default {
  name: 'Sticker',
  props: ['src'],
  data() {
    return {};
  },
  computed: {
    path() {
      let folder = this.src.split('-');
      folder.pop();

      return `static/sticker/${folder.join('-')}/${this.src}.png`;
    }
  },
  mounted() {},
  methods: {}
};



